<template>
  <div class="audit-chart-view" :id="elId" :style="styleObject"></div>
</template>

<script>
import * as echarts from 'echarts'

export default {
  name: 'ChartView',
   //
    props:{
        type: String, //  bar: line: pie 
        options: Object, // echarts
        width: String, //
        height: String, //
    },
  data () {
    return {
      elId: '',//id
      myChart: '', //
      //
      defaultOptions: {}
    }
  },
  created () {
    this.elId = 'chartView' + new Date().getTime() + Math.random();
  },
  //
  beforeDestroy () {
      //
      if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
      }
  },
  //
  computed: {
    //
    styleObject: function () {
        return {
            width: this.width || '100%',
            height: this.height || '100%',
        }
    }
  },
  //
  watch: {
      options: {
          handler: function(){
              this.initChart()
          },
          deep: true
      },
  },
  //
  methods: {
      //
      initChart: function () {
          var myOption = {};
          //
          if(this.options){
              myOption = $.extend(myOption, this.options)
          }
          if (this.myChart) {
              this.myChart.dispose();
              this.myChart = null;
          }
          //
          this.$nextTick( () => {
              //
              this.myChart = echarts.init(document.getElementById(this.elId));
              this.myChart.setOption(myOption);
              //
              $(window).resize( () => {
                    this.myChart && this.myChart.resize();
                });
              //,
              this.myChart.off('click');
              this.myChart.on('click', (params) => {
                  this.$emit('map-click', params);
              });
          })
      }
  }
}
</script>



