<template>
  <div class="xpanel xpanel-center-first">
    <div class="title left" style="height: 17.6%"></div>
    <div class="xpanel-look-more-text" @click="pageChange('data')">
      
    </div>
    <div class="xpanel-box-table">
      <div class="tablebox">
        <div class="tbl-header">
          <table border="0" cellspacing="0" cellpadding="0">
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody style="opacity: 0"></tbody>
          </table>
        </div>

        <div class="tbl-body">
          <table id="mytable" border="0" cellspacing="0" cellpadding="0">
            <thead>
              <tr style="height: 20%">
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody></tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import indexMixin from "../../mixins/indexMixin.js";

export default {
  name: "RiskData",
  mixins: [indexMixin],
  data() {
    return {
      MyMarhq: null,
      riskList: [
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx200",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx201",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx202",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx202",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx203",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx204",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx205",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx206",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx207",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx208",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx209",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx210",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx211",
        },
        {
          operTime: "2019-07-11 02:0000",
          riskType: "ESB",
          riskRank: "",
          content: "xxx212",
        },
      ],
    };
  },
  mounted: function () {
    this.doQuery(true);
  },
  //
  beforeDestroy: function () {
    clearInterval(this.MyMarhq);
  },
  methods: {
    initChart: function () {
      var that = this;

      that.MyMarhq = null;
      clearInterval(that.MyMarhq);
      $(".tbl-body tbody").empty();
      $(".tbl-header tbody").empty();
      var str = "";
      $.each(that.riskList, function (i, item) {
        str =
          "<tr>" +
          "<td>" +
          item.operTime +
          "</td>" +
          "<td>" +
          item.riskType +
          "</td>" +
          "<td>" +
          item.riskRank +
          "</td>" +
          "<td>" +
          item.content +
          "</td>" +
          "</tr>";

        $(".tbl-body tbody").append(str);
        $(".tbl-header tbody").append(str);
      });

      if (that.riskList.length > 4) {
        $(".tbl-body tbody").html(
          $(".tbl-body tbody").html() + $(".tbl-body tbody").html()
        );
        $(".tbl-body").css("top", "0");
        var tblTop = 0;
        var speedhq = 80; // 
        var outerHeight = $(".tbl-body tbody").find("tr").outerHeight();

        function Marqueehq() {
          if (tblTop <= -outerHeight * (that.riskList.length - 4)) {
            tblTop = 0;
            that.doQuery(false);
          } else {
            tblTop -= 1;
          }
          $(".tbl-body").css("top", tblTop + "px");
        }

        that.MyMarhq = setInterval(Marqueehq, speedhq);

        // 
        $(".tbl-body tbody").hover(
          function () {
            clearInterval(that.MyMarhq);
          },
          function () {
            clearInterval(that.MyMarhq);
            that.MyMarhq = setInterval(Marqueehq, speedhq);

            $(this).parent().parent().find("tr").removeClass("selected"); //
          }
        );
        $(document).ready(function () {
          $(".tbl-body tbody tr").click(function () {
            $(this).attr("title", $(this).context.cells[3].innerText);
            $(this)
              .addClass("selected") //
              .siblings()
              .removeClass("selected") //
              .end();
          });
        });
      }
    },

    //
    doQuery: function (flas) {
      var that = this;
      var results = [
        {
          operTime: "20210201",
          riskType: "1",
          riskRank: "",
          content: "1",
        },
        {
          operTime: "20210202",
          riskType: "2",
          riskRank: "",
          content: "2",
        },
        {
          operTime: "20210203",
          riskType: "3",
          riskRank: "",
          content: "3",
        },
        {
          operTime: "20210204",
          riskType: "4",
          riskRank: "",
          content: "4",
        },
        {
          operTime: "20210205",
          riskType: "5",
          riskRank: "",
          content: "5",
        },
        {
          operTime: "20210206",
          riskType: "6",
          riskRank: "",
          content: "6",
        },
        {
          operTime: "20210207",
          riskType: "7",
          riskRank: "",
          content: "7",
        },
        {
          operTime: "20210208",
          riskType: "8",
          riskRank: "",
          content: "8",
        },
      ];
      that.riskList = results;
      if (flas) {
        that.initChart();
      } else {
        $(".tbl-body tbody").empty();
        // $('.tbl-header tbody').empty();
        var str = "";
        $.each(that.riskList, function (i, item) {
          str =
            "<tr>" +
            "<td>" +
            item.operTime +
            "</td>" +
            "<td>" +
            item.riskType +
            "</td>" +
            "<td>" +
            item.riskRank +
            "</td>" +
            "<td>" +
            item.content +
            "</td>" +
            "</tr>";

          $(".tbl-body tbody").append(str);
          $(document).ready(function () {
            $(".tbl-body tbody tr").click(function () {
              $(this).attr("title", $(this).context.cells[3].innerText);
              $(this)
                .addClass("selected") //
                .siblings()
                .removeClass("selected") //
                .end();
            });
          });
          // $('.tbl-header tbody').append(str);
        });
      }
    },
    pageChange: function (key) {
      this.$emit("page-change", key);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



